<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="audit_button">
        <!-- <Button type="primary" @click="handleSubmit"></Button> -->
        <Button type="primary" @click="addDraft"></Button>
      </div>
      <Table :columns="columns" :data="tableData">
      <template slot-scope="{ row}" slot="action">
        <div>
          <button @click="btnOperate(1, row)"></button>
          <button @click="btnOperate(2, row)"></button>
          <button @click="btnOperate(3, row)"></button>
          <button @click="btnOperate(4, row)"></button>
        </div>
      </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="pageObj.pageIndex"
        :total="pageObj.total"
        :page-size="pageObj.pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <templateEditor :alertShow="editorShow"
      :alertName="alertShow.editorName"
      :alertCode="alertShow.editorCode"
      :alertToken="alertShow.editorToken"
      :alertTypeId="alertShow.editorTypeId"
      :alertContent="alertShow.editorContent"
      :alertDocument="alertShow.documentType"
      :alertDisType="alertShow.disType"
      @alertConfirm="editorSave"
      @alertCancel="alertCanc('editor')"
      alertTitle=""></templateEditor>
  </div>
</template>
<script>
import {mapActions} from 'vuex'
import listSearch from "@/components/list-search/search.vue";
import templateEditor from './template'
// const columns =[{title:''}, {title:""},{title:''},{title:''},{title:""},{title:""},{title:""}
// ,{title:"",width:300,slot:"action"}]
export default {
  components: {
    listSearch,
    templateEditor
  },
  data() {
    return {
      pageObj: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      searchData: {
        tempName: "",
        regName:"",
        tempType:[]
      },
      operateObj: {
        2: {type: 1, content: '', func: this.deleteTemplate},
        4: {type: 4, content: '', func: this.submitTemplate}
      },
      editorShow: false,
      alertShow: {
        disType: false,
        find: false,
        editor: false,
        editorDest: false,
        editorName: null,
        editorCode: null,
        editorToken: null,
        editorTypeId: null,
        editorContent: null,
        editorTempId: null,
        documentType: null,
        reas: false,
        reasText: '',
        subm: false,
        sumbId: null
      },
      searchArr: [
        {
          type: "input",
          value: "tempName",
          label: "",
          placeholder: ""
        },
        {
          type: "input",
          value: "regName",
          label: "",
          placeholder: ""
        },
        {
          type: "select",
          value: "tempType",
          label: "",
          placeholder: "",
          arr: [
            { label: "", value: 0 },
            { label: "", value: 1 },
            { label: "", value: 2 }
          ],
        }
      ],
      columns: [
        {title:'', key: 'tempName'},
        {title:"", key: 'caseType'},
        {title:'', key: 'registerName'},
        {title:'', key: 'caseType'},
        {title:"", key: 'createTime'},
        {title:"", key: 'updateTime'},
        {title:"", key: 'updateTime'},
        {title:"", key: 'status'},
        {title:"",width:300,slot:"action"}
      ],
      tableData:[{}]
    };
  },
  mounted () {
    this.getList()
  },
  methods: {
    ...mapActions({
      getTemplateList: 'organizePeriod/getTemplateList',
    }),
    goSearch() {},
    //  1: 2: 3 4:
    btnOperate(type, data) {
      this.confirmOperate(type)
    },
    confirmOperate (type) {
      this.$Modal.confirm({
          title: '',
          content: '<div style="color:#3B4453;text-align:center;font-size:18px">'+this['operateObj'][type]['content']+'</div>',
          onOk: () => {
              this['operateObj'][type]['func']()
          },
          onCancel: () => {
              
          }
      });
    },
    handleLook(row){
        
    },
    handleOver(row){
      this.$Modal.confirm({
          title: '',
          content: '<div style="color:#3B4453;text-align:center;font-size:18px"></div>',
          onOk: () => {
              this.$Message.success('');
              this.$Message.error('')
          },
          onCancel: () => {
              
          }
      });
    },
    backCase(row){
        this.$Modal.confirm({
            title: '',
            content: '<div style="color:#3B4453;text-align:center;font-size:18px"></div>',
            onOk: () => {
                this.$Message.success('');
                this.$Message.error('')
            },
            onCancel: () => {
               
            }
        });
    },
    // 
    deleteTemplate () {
      console.log(11)
    },
    // 
    submitTemplate () {
      console.log(22)
    },
    // 
    getList () {
      let params = {}
      Object.assign(params, this.pageObj)
      params.menuIdentifer = "MANAGE_TEMPLATE_LIST"
      this.getTemplateList (params)
        .then(res => {
          console.log(res)
          if (res.data.flag) {
            this.tableData = res.data.data.data
          } else {
            this.$Message.error(res.data.message)
          }
        })
        .catch(err=> {
          console.log(err);
        })
    },
    // 
    addDraft () {
      this.editorShow = true
    },
    // 
    editorSave () {

    },
    // 
    alertCanc () {
      this.editorShow = false
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
.wrap {
  .table_box {
    margin-top: 10px;
    background: #fff;
  }
  .audit_button {
    padding: 10px 0;
    display: flex;
    justify-content: flex-end;
    padding-right: 20px;
    // margin-bottom: 20px;
    button:first-child {
      margin-right: 10px;
    }
  }
}
</style>
